export const mediaInTabs = [];
export const tabExpanded = {};
export const tabsInfo = {};
export const tabCollections = []; //contains tabs IDs
export function getTabsFromCollection(tabId) {
    return Object.values(tabsInfo).filter(tabInfo => tabInfo.id === tabId);
}
export function getMediaByTabUuid(uuid, type) {
    return mediaInTabs.find(mediaItem => mediaItem.uuid === uuid && mediaItem.type === type);
}
